@import "mixins";

.g-tabbar {
   display: flex;
   width: 100%;
   height: $tabbar-height;
   background-color: #fff;

   &-item {
     flex: 1;
     @include flex-center(column);

     .iconfont {
       margin-bottom: 4px;
       font-size: $icon-font-size;
     }
   }
 }



